import { Injectable } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { LandingPageListModel, LandingPageModel } from '../models/dashboard';
import { RestApiService } from './rest-api.service';
import Swal from 'sweetalert2';

@Injectable({
  providedIn: 'root'
})
export class DataManagementService {
  landingAnimation = false;
  loading = false;
  loadingData = true;
  croppedImage: any;
  imgURL: any;
  landingPageData: LandingPageModel;
  landingPageList: LandingPageListModel[];
  landingPagesData: LandingPageModel[];

  constructor(
    private router: Router,
    private api: RestApiService,
    private sanitizer: DomSanitizer,
  ) { }

  startLoadingData() {
    this.loadingData = true;
  }

  stopLoadingData() {
    this.loadingData = false;
  }

  startLoading() {
    this.loading = true;
  }

  stopLoading() {
    this.loading = false;
  }

  async removeBackground(formData: FormData) {
    this.startLoading();
    let Imageresp: {image: string, details: string} = await this.api.cropImage(formData, this.removeBackgroundErrorHandler).toPromise();

      // const imageObjectURL = URL.createObjectURL(imageBlob);
      // this.croppedImage = this.sanitizer.bypassSecurityTrustResourceUrl(imageObjectURL);
      this.croppedImage = Imageresp.image
      this.stopLoading()

    return this.croppedImage;
  }

  swalErrorAlert(message: string) {
    Swal.fire({
      title: 'Error',
      text: message,
      icon: 'error',
    });
  }
  removeBackgroundErrorHandler = (error: any) => {
    this.stopLoading();
    let message = "Something Went Wrong!";
    switch (error.status) {
      case 400:
      message = "BAD_REQUEST"
        break;
      case 404:
        message = "NOT_FOUND"
      break;
      case 500:
        message = "INTERNAL_SERVER"
        break;
      case 401:
        message = "FACE_TO_CLOSE"
      break;
      case 402:
        message = "NO_FACE_DETECTED"
        break;
      case 403:
        message = "UPLOAD_ERROR"
      break;
      case 405:
        message = "PHOTOROOM_ERROR"
        break;
      case 406:
        message = "JIMP_ERROR"
      break;
      default:
        break;
    }
    this.router.navigate(['/']);
    this.swalErrorAlert(message);
  }

  async getAllLandingPagesList() {
    this.startLoadingData();
    this.landingPageList = await this.api.getAllLandingPagesList().toPromise();
    this.stopLoadingData();
  }

  async getAllLandingPages() {
    this.startLoadingData();
    this.landingPagesData = await this.api.getAllLandingPages().toPromise();
    this.landingPageData = this.landingPagesData[0];
    this.stopLoadingData();
  }

}
