import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class RestApiService {

  // Define API
  apiBase = 'https://fathomless-bastion-96634.herokuapp.com/api';
  apiURL = 'https://sdk.photoroom.com/v1';
  apiKey = 'fd24a7481f4d27d6741052516e4b671eb2c83216';

  constructor(private http: HttpClient) { }

  // Http Options
  private headers = new HttpHeaders({ 'Content-Type': 'application/json' });



  cropImage(formData: any, errorHandler: any = undefined) : Observable<any> {
    return this.callImage('post','upload/2', new HttpHeaders({ }) ,formData, errorHandler);

    // return this.post('upload/2', new HttpHeaders({ }) ,formData, errorHandler);
  }


  getAllLandingPagesList(errorHandler: any = undefined){
    return this.get('documents/list', errorHandler);
  }

  createNewDocument(documentData ,errorHandler: any = undefined){
    return this.post('documents', null, documentData, errorHandler);
  }

  getAllLandingPages(errorHandler: any = undefined){
    return this.get('documents', errorHandler);
  }


  private post(path: string, headers = null ,params: any, errorHandler: any = undefined) {
    return this.call('post', path, headers, params, errorHandler);
  }

  private patch(path: string, params: any, errorHandler: any = undefined) {
    return this.call('patch', path, params, errorHandler);
  }

  private get(path: string, params: any, errorHandler: any = undefined) {
    return this.call('get', path, params, errorHandler);
  }


  private put(path: string, params: any, errorHandler: any = undefined) {
    return this.call('put', path, params, errorHandler);
  }

  private delete(path: string, params: any, errorHandler: any = undefined) {
    return this.call('delete', path, params, errorHandler);
  }

  private download(
    path: string,
    params: any = undefined,
    errorHandler: any = undefined
  ) {
    if (!errorHandler) errorHandler = this.handleError;
    return this.http
      .get<any>(`${this.apiBase}/${path}`, {
        params,
        headers: this.headers,
        responseType: 'blob' as 'json',
      })
      .pipe(catchError(errorHandler));
  }


  callImage(method, path: string, headers: any, params: any, errorHandler: any = undefined) {
    if (!errorHandler) errorHandler = this.handleError;
    // console.debug(`LSG API request '${method}'`, `${ this.apiUrl }/${ path }`, params);
    return this.http
      .request<any>(method, `${this.apiBase}/${path}`, {
        body: params,
        headers: !!headers ? headers : this.headers,
        responseType: 'blob' as 'json',
      })
      .pipe(
        map((resp) => {
          if (resp.error) {
            console.error('Error', resp);
            throw resp.error;
          }
          return resp;
        }),
        catchError(errorHandler)
      );
  }

  call(method, path: string, headers: any, params: any, errorHandler: any = undefined) {
    if (!errorHandler) errorHandler = this.handleError;
    // console.debug(`LSG API request '${method}'`, `${ this.apiUrl }/${ path }`, params);
    return this.http
      .request<any>(method, `${this.apiBase}/${path}`, {
        body: params,
        headers: !!headers ? headers : this.headers,
      })
      .pipe(
        map((resp) => {
          if (resp.error) {
            console.error('Error', resp);
            throw resp.error;
          }
          return resp;
        }),
        catchError(errorHandler)
      );
  }

   // Error handling
   handleError(error) {
    let errorMessage = '';
    if(error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);
    return throwError(errorMessage);
 }
}
